<template>
  <!-- <button v-for="n in markers" :key="n.id" @click="onClick(n)">
    #{{ n.id }}
  </button> -->
  <yandex-map
    ref="map"
    :coords="[55.72, 37.65]"
    zoom="10"
    style="width: 100%; height: 100vh"
    :controls="[]"
  >
    <ymap-marker
      v-for="n in markers2"
      :key="n.id"
      :marker-id="n.id"
      marker-type="Rectangle"
      :coords="n.coord"
      :balloon="{ body: n.text }"
      :marker-stroke="{
        Reactangle: { color: '#ffff', opacity: 0.1, width: 100 },
      }"
    ></ymap-marker>
  </yandex-map>
</template>
<script>
import { yandexMap, ymapMarker } from "vue-yandex-maps";
import axios from "axios";

export default {
  name: "App",
  components: {
    yandexMap,
    ymapMarker,
  },
  data() {
    return {
      url: "http://78.47.77.103:8081/MoscowData/GetByID/",

      markers2: [],
    };
  },
  methods: {
    onClick(m) {
      this.$refs.map.myMap.balloon.open(m.coord, m.text);
    },
    async fetchData() {
      for (let i = 0; i <= 10000; i++) {
        try {
          const res = await axios.get(this.url + i);

          //   let gem1 = res.data.geometry_1.split(",").map((string) => +string);
          //   let gem2 = res.data.geometry_2.split(",").map((string) => +string);

          this.markers2.push({
            coord: [
              res.data.geometry_1.split(",").map((string) => +string),
              res.data.geometry_2.split(",").map((string) => +string),
            ],
            id: i,
          });
          console.log(this.markers2);
        } catch (error) {
          return error;
        }
      }
    },
  },
  created() {
    this.fetchData();
  },
};
</script>

<style scoped lang="scss">
.ymaps-2-1-79-gotoymaps__container {
  display: none !important;
}
